//@flow
import * as React from 'react';
import { step1Endpoint } from './Constants';

type Props = {
  index: string,
  offer: Object,
};

const Step1 = (props: Props): React.Element<'div'> => {
  const { offer } = props;
  const { OfferId, FormId } = offer;
  const endpoint = step1Endpoint({ formId: FormId, offerId: OfferId });
  return (
    <div className="offer_action">
      <div className="next_btn">
        <a href={`/${endpoint.redirectUrl}`}>Continue Offer</a>
      </div>
    </div>
  );
};

export default Step1;
