import { FORM_ENDPOINT } from 'components/FormBuilder/Constants';
import { APP_BASE_NAME } from 'shared/Consts';

export const step1Endpoint = ({ offerId, formId }) => {
  const formBaseEndpoint = `${FORM_ENDPOINT}/${formId}/items/${offerId}`;
  const redirectUrl = `${APP_BASE_NAME}/offer?offerId=${offerId}&step=1&formId=${formId}`;
  return {
    formBaseEndpoint,
    formSubmissionEndpoint: `${formBaseEndpoint}/submit`,
    redirectUrl,
  };
};
